<!-- 入口列表 -->
<template>
<div id="entrance">
	<p v-for="i in 10000" >{{i}}</p>
	<!-- <pre  class="input"  contenteditable="true" @click="focus" @blur="blur">{{msg}}</pre> -->
	<!-- <input  class="input" type="text" v-model="msg"> -->
</div>	
</template>

<script>
import Api from '../../service/api.js'
import { getBaseData } from '../../vuex/actions/common.js'

export default {
	data() {
		return {
			paths: [], //面包屑导航数据
			path: '',
		imagesUrl: '',
		msg: ''
		}
	},
	vuex: {
		getters: {
			baseData: state => state.common.baseData,
		},
		actions: {
			getBaseData,
		},
	},
	computed: {
		path() {
			return this.$route.path;
		},

	},
	methods: {
		focus: function () {
			$('.input').focus();
			
			$('body').scrollTop( $('body').height() )
		},
		blur: function () {
			
		},
		fnScroll: function () {
			console.log(1)
			$('.input')[0].blur();
		}
	},
	route: {
		activate: function(transition) {
			$('#router').on('scroll', this.fnScroll);
			transition.next();
		},
	},
	components: {
	}

}


</script>

<style>
@import '../../common/css/sass.scss';
.input{ position: fixed; z-index: 9999; left: 0; bottom: 0; margin: 0; width: 100%; height: px2rem(48px); line-height: px2rem(48px); }
body{ position: fixed; width: 100%; height: 100%; overflow: hidden; }
#router {
/* main绝对定位，进行内部滚动 */
position: absolute;
top: 50px;
bottom: 34px;
/* 使之可以滚动 */
 overflow-y: scroll;
  /* 增加该属性，可以增加弹性，是滑动更加顺畅 */
  -webkit-overflow-scrolling: touch;   
}

main .content {
    height: 2000px;
}
</style>


